@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body{background-color:#222831;margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.button{background-color:rgba(0,0,0,0);border:2px solid #ca4095;color:black;padding:15px 32px;text-align:center;text-decoration:none;display:inline-block;font-size:16px;border-radius:4px}.share-root{display:flex;flex-direction:row;justify-content:center;background-color:#222831;height:100vh}.share-root .share-content{margin:8px;border-radius:4px}.share-root .file-drop-area{position:relative;background-color:#ececec;z-index:1;border-width:1;border-style:solid;border-radius:4px;margin:4px;margin-top:20vh;min-width:360px;min-height:260px;padding-top:80px;text-align:center;cursor:pointer}.share-root .file-drop-area img{margin:8px}.share-root .hide-overlay{opacity:0;transition-duration:750ms}.share-root .show-overlay{opacity:1;transition-duration:750ms;position:absolute;width:100vw;height:100vh;background-color:rgba(0,0,0,0.5)}.modal{position:absolute;top:50%;left:50%;background-color:#ececec;border-radius:8px;transform:translate(-50%, -50%)}.ReactModal__Overlay{opacity:0;transition:opacity 200ms ease-in-out}.ReactModal__Overlay--after-open{opacity:1}.ReactModal__Overlay--before-close{opacity:0}.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.5)}.modal-root{display:flex;flex-direction:row;justify-content:center;padding:40px}.modal-root .modal-body{text-align:center}.modal-root .qrcode{width:30vh !important;height:30vh !important;padding:8px}.modal-root img{width:30vh;height:calc(30vh * 868 / 1257)}.modal-root .button{margin-top:16px}.modal-root .url-copy{position:relative;display:flex}.modal-root .url-copy-input{position:relative;flex:1 1 auto}.modal-root .url-copy-button{box-sizing:border-box;position:relative;width:40px;height:40px;border-width:1px;border-style:solid;border-color:black;padding:4px}.modal-root .url-copy-button img{box-sizing:border-box;width:100%;height:100%}.share-text{max-width:80vw;word-wrap:break-word}.download-root{display:flex;flex-direction:column;align-items:center;height:100vh;background-color:#222831}.download-root .download-border{padding:80px}.download-root .download-body{text-align:center;background-color:white;padding:60px;border-radius:8px}.download-root img{width:30vh;height:calc(30vh * 868 / 1257)}.download-root .button{margin-top:16px}

